.App {
  text-align: center;
}
.container {
  display: flex;
    /* Center vertically */
          /* Full viewport height */
}

.centered-div {
  width: 100%;           /* Set width of the div */
    margin-top: 20px;     /* Set height of the div */
    margin-bottom: 20px;     /* Set height of the div */
  
  text-align: center;     /* Center text inside the div */
  
}

.news-card {
  height: 550px; /* Fixed height */
  display: flex;
  margin-left: 20px;
  width: 20rem;
  flex-direction: column;
  justify-content: space-between;
}

.news-card img {
  height: 90px; /* Fixed image height */
  object-fit: cover; /* Ensures the image fits within the given height */
}

.news-card .card-body {
  flex-grow: 1; 
}

/* .news-card .card-title {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
} */

.news-card .card-text {
  flex-grow: 1; /* Ensures the card description expands to fill the available space */
  overflow: hidden;
  text-overflow: ellipsis; /* Handles long descriptions */
  max-height: 50px; /* Adjust to limit the description height */
}
.news-card {
  height: 100%; /* Ensure card takes full height of the container */
  display: flex;
  flex-direction: column;
}

@media (max-width: 576px) {
  .news-card {
    margin-bottom: 15px; /* Add some spacing between cards on mobile */
  }
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
